<template>
	<view>
		<uni-swiper-dot :info="info" :current="current" field="title" :mode="mode">
		    <swiper class="swiper-box" @change="change">
		        <swiper-item v-for="(item ,index) in info" :key="index">
		            <view class="swiper-item">
						<image class="swiper-item" :src="getImgUrl(item.url)"></image>
		            </view>
		        </swiper-item>
		    </swiper>
		</uni-swiper-dot>
	</view>
</template>

<script>
	import uniSwiperDot from "@/components/uni-swiper-dot/uni-swiper-dot.vue"
	
	export default {
		props: ["info"],
		components:{
			uniSwiperDot
		},
		name: "homeBanner",
		data() {
			return {
				current: 0,
				mode: 'nav'
			};
		},
		methods:{
			getImgUrl(ImgSrc){
				if (ImgSrc == null || ImgSrc == "" ) {
					return "../../static/img/base/default.png";
				}
				if (ImgSrc.substr(0,4) == "http") {
					return ImgSrc;
				} else if (ImgSrc.substr(0,1) == "/") {
					return this.baseUrl+`${ImgSrc}`;
				} else {
					return this.baseUrl+`/${ImgSrc}`;
				}
			},
			change(e) {
				this.current = e.detail.current;
			}
		}
	}
</script>

